.egift-section {
	position: relative;
	text-align: center;
	padding: 0 0 5rem;
	@include max-md {
		padding: 0 0 3rem;
	}
	.orn-1 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 120px;
		transform: translate(68%, 0) rotate(-15deg) scaleX(-1);
		z-index: 3;
		pointer-events: none;
		@include min-md {
			width: 140px;
			transform: translate(63%, 0) rotate(-15deg) scaleX(-1);
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-60%, 0);
		z-index: 3;
		pointer-events: none;
		@include min-md {
			width: 160px;
			transform: translate(-50%, 0);
		}
		@include min-lg {
			width: 180px;
		}
		.orn-3 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 80px;
			transform: translate(-30%, 60%) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			height: 100%;
			.gift-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	.gift-wrap {
		position: relative;
	}
	.tabs-gift {
		position: relative;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: auto;
		width: fit-content;
		padding: 0.25rem;
		border-radius: var(--border-radius);
		gap: 0.5rem;
		&::before {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: var(--color-secondary);
			opacity: 0.25;
			content: "";
			border-radius: 50px;
		}
		@include max-md {
			padding: 0.25rem;
		}
		.btn-custom {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			padding: 0.5rem;
			min-width: 140px;
			text-align: center;
			background-color: transparent;
			color: var(--color-button-trans);
			z-index: 2;
			@include max-md {
				min-width: 120px;
				svg {
					width: 25px;
					height: 25px;
				}
			}
			path {
				fill: var(--color-button-trans);
				transition: all 0.2s ease;
			}
			&.active {
				color: var(--text-button);
				path {
					fill: var(--text-button);
				}
			}
			&:focus {
				box-shadow: none;
			}
		}
		.glider {
			position: absolute;
			left: 0;
			top: 0;
			background: var(--color-secondary);
			width: 100%;
			height: 100%;
			border-radius: 50px;
			z-index: -1;
		}
	}
	.card {
		.card-body {
			padding: 3rem;
			@include max-md {
				padding: 3rem 1.25rem;
			}
			@include max-xs {
				padding: 3rem 0.75rem;
			}
		}
	}
	form {
		position: relative;
		z-index: 5;
		.input-group-text {
			background-color: var(--color-secondary);
			border-color: var(--color-secondary);
			color: var(--text-button);
		}
		@include max-md {
			.row {
				gap: 1rem;
			}
		}
	}
	.gift,
	.angpao {
		display: none;
		transition: all 0.5s ease-in;
		opacity: 0;
		&.show {
			display: block;
			opacity: 1;
		}
	}
	.gift-content {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-dark);
		border-radius: var(--border-radius);
		padding: 3rem 2rem;
		margin-top: -8rem;
		@include max-md {
			padding: 2rem 1rem;
			margin-top: -5rem;
		}
	}
}

@keyframes tab {
	0% {
		transform: scale(0.5);
	}
	100% {
		transform: scale(1);
	}
}
