			transform: translate(50%, 50%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-16 {
			position: absolute;
			bottom: 100%;
			right: 0;
			width: 160px;
			transform: translate(10%, 30%);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 60%;
				transform: translate(10%, 55%);
			}
		}
		.orn-17 {
			position: absolute;
			bottom: 100%;
			right: 0;
			width: 100px;
			transform: translate(0, 30%);
			@include min-md {
				width: 140px;
				transform: translate(0, 40%);
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-18 {
			position: absolute;
			bottom: 100%;
			left: 0;
			width: 180px;
			transform: translate(-10%, 60%);
			@include min-md {
				width: 260px;
				transform: translate(-18%, 75%);
			}
			@include min-lg {
				width: 320px;
			}
		}
		.orn-19 {
			position: absolute;
			bottom: 100%;
			right: 0;
			width: 120px;
			transform: translate(-7%, 30%) scaleX(-1);
			@include min-md {
				width: 180px;
				transform: translate(-7%, 35%) scaleX(-1);
			}
			@include min-lg {
				width: 240px;
			}
			.orn-20 {
				position: absolute;
				top: 0;
				left: 0;
				width: 100px;
				transform: translate(-30%, -40%) scaleX(-1);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 60%);
		@include min-md {
			width: 240px;
			transform: translate(30%, 50%);
		}
		@include min-lg {
			width: 280px;
			transform: translate(30%, 40%);
		}
	}
	.orn-21 {
		position: absolute;
		top: 50%;
		left: 20%;
		width: 60px;
		transform: translate(-50%, -50%);
		@include min-md {
			width: 80px;
		}
		@include min-lg {
			width: 100px;
		}
	}
	.orn-22 {
		position: absolute;
		top: 25%;
		right: 15%;
		width: 40px;
		transform: translate(50%, -50%) scaleX(-1);
		@include min-md {
			width: 60px;
		}
		@include min-lg {
			width: 80px;
		}
	}
	.orn-23 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 80px;
		transform: translate(-30%, 60%) scaleX(-1);
		@include min-md {
			width: 100px;
			transform: translate(-40%, 60%) scaleX(-1);
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-24 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-10%, -30%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-25 {
		position: absolute;
		bottom: 18%;
		left: 18%;
		width: 80px;
		transform: translate(-50%, -50%);
		@include min-md {
			width: 100px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-26 {
		position: absolute;
		bottom: 25%;
		right: 18%;
		width: 80px;
		transform: translate(50%, -50%) scaleX(-1);
		@include min-md {
			width: 100px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.image-wrapper {
		position: relative;
		width: 85%;
		height: auto;
		aspect-ratio: 6/4;
		margin: 0 auto -5rem;
		z-index: 5;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			background-color: var(--color-primary);
			color: var(--color-dark);
			border-radius: var(--border-radius);
			padding: 5rem 2rem 3rem;
			@include max-md {
				padding: 5rem 1rem 2rem;
			}
			&::before {
				position: absolute;
				top: 50%;
				left: 50%;
				width: calc(100% - 1rem);
				height: calc(100% - 1rem);
				border: 1px solid var(--color-main);
				border-radius: var(--border-radius);
				content: "";
				transform: translate(-50%, -50%);
				pointer-events: none;
			}
			.thank-body {
				position: relative;
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
