.story-section {
	position: relative;
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-30%, 0);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 220px;
		}
		.orn-2 {
			position: absolute;
			bottom: 100%;
			right: 20%;
			width: 100px;
			transform: translate(0, 50%);
			@include min-md {
				right: 0;
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
	}
	.orn-3 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(20%, -20%) rotate(-25deg) scaleX(-1);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 240px;
		}
		.orn-4 {
			position: absolute;
			top: 0;
			left: 20%;
			width: 100px;
			transform: translate(-50%, -20%);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.story {
		position: relative;
		padding: 0 0 5rem;
		margin-top: -10rem;
		@include max-md {
			padding: 0 0 3rem;
			margin-top: -5rem;
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		background-color: var(--color-primary);
		color: var(--color-dark);
		padding: 2rem;
		border-radius: var(--border-radius);
		@include max-md {
			padding: 2rem 1rem;
		}
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			width: calc(100% - 1rem);
			height: calc(100% - 1rem);
			border: 1px solid var(--color-main);
			border-radius: var(--border-radius);
			content: "";
			transform: translate(-50%, -50%);
			pointer-events: none;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 16/9;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
			}
		}
	}
}
