.venue-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 50%;
		left: 50%;
		width: 150%;
		pointer-events: none;
		transform: translate(-49%, -50%);
	}
	.orn-2 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-55%, -20%);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-3 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 120px;
		transform: translate(70%, 0);
		@include min-md {
			width: 240px;
		}
		@include min-lg {
			width: 260px;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 15%;
		right: 0;
		width: 120px;
		transform: translate(68%, 0) rotate(-10deg) scaleX(-1);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 5%;
		left: 0;
		width: 140px;
		transform: translate(-60%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 80px;
			transform: translate(-30%, 60%) scaleX(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-20%, -50%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-8 {
		position: absolute;
		bottom: 100%;
		left: 0;
		width: 140px;
		transform: translate(-30%, -5%) rotate(-15deg);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-9 {
		position: absolute;
		bottom: 100%;
		right: 0;
		width: 140px;
		transform: translate(40%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-10 {
		position: absolute;
		top: 0;
		right: 0;
		width: 140px;
		transform: translate(30%, -50%) scaleX(-1);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			.card {
				background-color: transparent;
				z-index: 5;
				text-align: center;
				border: none;
				.card-body {
					position: relative;
					padding: 3rem 2rem;
					@include max-lg {
						padding: 0;
					}
					h4 {
						margin: 0 0 1rem;
					}
					hr {
						display: block;
						width: 25%;
						height: 2px;
						margin: 0 auto 1rem;
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.event-name {
		position: relative;
		z-index: 3;
		.date {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			width: 65%;
			background-color: var(--color-primary);
			color: var(--color-dark);
			border-radius: 100px;
			margin: 0 auto 0;
			padding: 5rem 1rem;
			@include max-md {
				width: 75%;
			}
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				margin-bottom: 0;
				font-size: 32px;
				min-width: 30%;
			}
		}
		h4 {
			font-family: $font-secondary;
			padding-bottom: 5rem;
			@include max-md {
				padding-bottom: 6rem;
			}
		}
		p {
			margin-bottom: 0;
			font-weight: 600;
		}
	}
	.event-place {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-dark);
		padding: 6rem 2rem 2rem;
		border-radius: var(--border-radius);
		@include max-md {
			padding-top: 7rem;
		}
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			width: calc(100% - 1rem);
			height: calc(100% - 1rem);
			border: 1px solid var(--color-main);
			border-radius: var(--border-radius);
			content: "";
			transform: translate(-50%, -50%);
			pointer-events: none;
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
