.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	width: 100%;
	transform: translateZ(0);
	&.cover-opened {
		transform: translateY(-100%);
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 140px;
		transform: translate(60%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(0, 60%);
		@include min-md {
			width: 50%;
		}
		@include min-lg {
			width: 45%;
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 30%);
		@include min-md {
			width: 180px;
			transform: translate(-50%, 20%);
		}
		@include min-lg {
			width: 240px;
			transform: translate(-50%, 0%);
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 10%;
		width: 200px;
		transform: translate(0, 60%);
		@include min-md {
			right: 0;
			width: 50%;
		}
		@include min-lg {
			width: 45%;
		}
		.orn-6 {
			position: absolute;
			bottom: 100%;
			right: 0;
			width: 120px;
			transform: translate(0, 50%);
			@include min-md {
				right: 10%;
				width: 180px;
			}
			@include min-lg {
				width: 240px;
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 60%);
		@include min-md {
			width: 240px;
			transform: translate(30%, 50%);
		}
		@include min-lg {
			width: 280px;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		transition: opacity 1s ease;
		height: 100vh;
		@include max-lg {
			padding: 4rem 0 6rem;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			color: var(--color-dark);
			height: 100%;
			@include max-lg {
				display: flex;
				justify-content: center;
				align-items: center;
			}
			.cover-body {
				position: relative;
				width: 60%;
				margin: 0 auto;
				padding: 1rem 0;
				@include max-lg {
					width: 80%;
					margin-bottom: 8rem;
				}
				@include max-md {
					width: 100%;
				}
				.greeting-wrapper {
					position: absolute;
					top: 2rem;
					left: 50%;
					transform: translate(-50%, 0);
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				h6 {
					font-weight: 600;
				}
				.image-wrapper {
					position: relative;
					width: 100%;
					height: auto;
					aspect-ratio: 6/3.5;
					@include max-lg {
						aspect-ratio: 4/3;
					}
					@include max-md {
						aspect-ratio: 1;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: var(--border-radius);
						position: relative;
					}
				}
				.cover-description {
					position: absolute;
					bottom: 0;
					left: 50%;
					width: 60%;
					padding: 2rem;
					text-align: center;
					background-color: var(--color-primary);
					color: var(--color-dark);
					border-radius: var(--border-radius);
					transform: translate(-50%, 50%);
					&::before {
						position: absolute;
						top: 50%;
						left: 50%;
						width: calc(100% - 1rem);
						height: calc(100% - 1rem);
						border: 1px solid var(--color-main);
						border-radius: var(--border-radius);
						content: "";
						transform: translate(-50%, -50%);
						pointer-events: none;
					}
					@include max-lg {
						width: 70%;
					}
					@include max-md {
						width: 90%;
						padding: 2rem 1rem;
					}
					* {
						position: relative;
					}
					.title {
						color: var(--color-dark);
					}
					.cover-event {
						position: relative;
						span,
						p,
						h6 {
							margin-bottom: 0;
						}
					}
				}
			}
			.elements-widget {
				position: relative;
				margin-top: 1rem;
				width: 100%;
			}
		}
	}
}
