.reminder-wrap {
	position: relative;
	display: block;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 120px;
		transform: translate(60%, 0);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-20%, 50%);
		@include min-md {
			width: 50%;
			transform: translate(-20%, 60%);
		}
		@include min-lg {
			width: 45%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			right: 0;
			width: 100px;
			transform: translate(20%, 0) scaleX(-1);
			@include min-md {
				width: 140px;
				transform: translate(20%, 20%) scaleX(-1);
			}
			@include min-lg {
				width: 160px;
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0%);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(30%, 60%);
		@include min-md {
			width: 50%;
			transform: translate(30%, 70%);
		}
		@include min-lg {
			width: 45%;
		}
		.orn-6 {
			position: absolute;
			bottom: 100%;
			right: 50%;
			width: 140px;
			transform: translate(75%, 5%);
			@include min-md {
				width: 160px;
				transform: translate(75%, 25%);
			}
			@include min-lg {
				width: 180px;
				transform: translate(75%, 35%);
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 140px;
		transform: translate(50%, 20%);
		@include min-md {
			width: 180px;
			transform: translate(50%, 40%);
		}
		@include min-lg {
			width: 220px;
		}
	}
	.orn-9 {
		position: absolute;
		top: 50%;
		left: 10%;
		width: 60px;
		transform: translate(-50%, -50%);
		@include min-md {
			width: 80px;
		}
		@include min-lg {
			width: 100px;
		}
	}
	.orn-10 {
		position: absolute;
		top: 10%;
		right: 20%;
		width: 40px;
		transform: translate(50%, -50%) scaleX(-1);
		@include min-md {
			width: 60px;
		}
		@include min-lg {
			width: 80px;
		}
	}
	.reminder-content {
		position: relative;
		width: fit-content;
		margin: 0 auto;
		max-width: 100%;
		z-index: 2;
	}
	.btn-reminder {
		z-index: 3;
		position: relative;
		margin: 0 auto;
		display: flex;
		align-items: center;
		position: relative;
		gap: 0.5rem;
		transition: all 0.2s ease-in-out;
		text-decoration: none;
		margin-top: 1.5rem;
		path {
			fill: var(--text-button);
			transition: all 0.3s ease-in-out;
		}
	}
}
