.header-section {
	position: relative;
	min-height: 100vh;
	width: 100%;
	.scroll-icon {
		position: relative;
		margin-top: 2rem;
		rect {
			stroke: var(--color-light);
		}
	}
	.orn-99 {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translateX(-50%);
		@include max-lg {
			width: 150%;
		}
		@include max-md {
			width: 250%;
		}
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0);
		@include min-md {
			width: 200px;
		}
		@include min-lg {
			width: 240px;
		}
	}
	.orn-2 {
		position: absolute;
		top: 5%;
		right: 0;
		width: 120px;
		transform: translate(60%, 0);
		@include min-md {
			width: 180px;
		}
		@include min-lg {
			width: 200px;
		}
	}
	.orn-3 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-20%, 50%);
		@include min-md {
			width: 50%;
			transform: translate(-20%, 65%);
		}
		@include min-lg {
			width: 45%;
		}
		.orn-8 {
			position: absolute;
			top: 0;
			right: 0;
			width: 100px;
			transform: translate(20%, 0) scaleX(-1);
			@include min-md {
				width: 120px;
				transform: translate(20%, 40%) scaleX(-1);
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-9 {
			position: absolute;
			top: 0;
			left: 45%;
			width: 160px;
			transform: translate(-50%, -70%);
			@include min-md {
				width: 240px;
				transform: translate(-50%, -50%);
			}
			@include min-lg {
				width: 280px;
			}
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			left: 40%;
			width: 140px;
			transform: translate(-50%, 0);
			@include min-md {
				width: 160px;
				transform: translate(-80%, -30%);
			}
			@include min-lg {
				width: 180px;
				transform: translate(-80%, -70%);
			}
		}
		.orn-11 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 140px;
			transform: translate(40%, -30%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 240px;
				transform: translate(40%, -60%);
			}
		}
		.orn-12 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 200px;
			transform: translate(0, -85%);
			@include min-md {
				bottom: 100%;
				width: 260px;
				transform: translate(-30%, 20%);
			}
			@include min-lg {
				right: unset;
				left: 16%;
				width: 60%;
				transform: translate(0%, 40%);
			}
			.orn-13 {
				position: absolute;
				bottom: 100%;
				left: 25%;
				width: 60px;
				transform: translate(-50%, 45%);
				@include min-md {
					width: 80px;
				}
				@include min-lg {
					width: 100px;
				}
			}
			.orn-14 {
				position: absolute;
				bottom: 100%;
				left: 60%;
				width: 60px;
				transform: translate(-50%, 50%);
				@include min-md {
					width: 80px;
				}
				@include min-lg {
					width: 100px;
				}
			}
			.orn-15 {
				position: absolute;
				bottom: 100%;
				right: 0;
				width: 60px;
				transform: translate(0%, 70%);
				@include min-md {
					width: 80px;
				}
				@include min-lg {
					width: 100px;
					transform: translate(-5%, 75%);
				}
			}
		}
	}
	.orn-4 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 140px;
		transform: translate(-50%, 0%);
		@include min-md {
			width: 160px;
		}
		@include min-lg {
			width: 180px;
		}
	}
	.orn-5 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 200px;
		transform: translate(0, 70%);
		@include min-md {
			width: 50%;
		}
		@include min-lg {
			width: 45%;
		}
		.orn-6 {
			position: absolute;
			bottom: 100%;
			right: 50%;
			width: 120px;
			transform: translate(50%, 50%);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 240px;
			}
		}
		.orn-16 {
			position: absolute;
			bottom: 100%;
			right: 0;
			width: 160px;
			transform: translate(10%, 30%);
			@include min-md {
				width: 260px;
			}
			@include min-lg {
				width: 60%;
				transform: translate(10%, 55%);
			}
		}
		.orn-17 {
			position: absolute;
			bottom: 100%;
			right: 0;
			width: 100px;
			transform: translate(0, 30%);
			@include min-md {
				width: 140px;
				transform: translate(0, 40%);
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-18 {
			position: absolute;
			bottom: 100%;
			left: 0;
			width: 180px;
			transform: translate(-10%, 60%);
			@include min-md {
				width: 260px;
				transform: translate(-18%, 75%);
			}
			@include min-lg {
				width: 320px;
			}
		}
		.orn-19 {
			position: absolute;
			bottom: 100%;
			right: 0;
			width: 120px;
			transform: translate(-7%, 30%) scaleX(-1);
			@include min-md {
				width: 180px;
				transform: translate(-7%, 35%) scaleX(-1);
			}
			@include min-lg {
				width: 240px;
			}
			.orn-20 {
				position: absolute;
				top: 0;
				left: 0;
				width: 100px;
				transform: translate(-30%, -40%) scaleX(-1);
				@include min-md {
					width: 120px;
				}
				@include min-lg {
					width: 140px;
				}
			}
		}
	}
	.orn-7 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 160px;
		transform: translate(30%, 60%);
		@include min-md {
			width: 240px;
			transform: translate(30%, 50%);
		}
		@include min-lg {
			width: 280px;
			transform: translate(30%, 40%);
		}
	}
	.orn-21 {
		position: absolute;
		top: 50%;
		left: 20%;
		width: 60px;
		transform: translate(-50%, -50%);
		@include min-md {
			width: 80px;
		}
		@include min-lg {
			width: 100px;
		}
	}
	.orn-22 {
		position: absolute;
		top: 25%;
		right: 15%;
		width: 40px;
		transform: translate(50%, -50%) scaleX(-1);
		@include min-md {
			width: 60px;
		}
		@include min-lg {
			width: 80px;
		}
	}
	.orn-23 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 80px;
		transform: translate(-30%, 60%) scaleX(-1);
		@include min-md {
			width: 100px;
			transform: translate(-40%, 60%) scaleX(-1);
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-24 {
		position: absolute;
		top: 0;
		left: 0;
		width: 120px;
		transform: translate(-10%, -30%);
		@include min-md {
			width: 140px;
		}
		@include min-lg {
			width: 160px;
		}
	}
	.orn-25 {
		position: absolute;
		bottom: 18%;
		left: 18%;
		width: 80px;
		transform: translate(-50%, -50%);
		@include min-md {
			width: 100px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.orn-26 {
		position: absolute;
		bottom: 25%;
		right: 18%;
		width: 80px;
		transform: translate(50%, -50%) scaleX(-1);
		@include min-md {
			width: 100px;
		}
		@include min-lg {
			width: 120px;
		}
	}
	.header-content {
		position: relative;
		height: 100%;
		.header {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			width: 50%;
			height: 100%;
			margin: 0 auto;
			padding: 8rem 0 0;
			.header-title {
				position: relative;
				text-align: center;
				&-content {
					position: relative;
					border-radius: var(--border-radius);
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
			.image-wrapper {
				position: relative;
				width: 80%;
				height: auto;
				aspect-ratio: 4/6;
				margin: 0 auto;
				align-self: flex-end;
				.header-background {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
				.image-element {
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					width: 90%;
					height: 93%;
					.header-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
					}
				}
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}

[data-anim="zoom-in-main"].has-animate {
	animation-name: zoomInMain;
	animation-fill-mode: forwards;
	animation-duration: 3500ms !important;
}

[data-anim="peafowl-right"].has-animate {
	animation-name: peafowlRight;
	animation-duration: 3500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: linear;
}
[data-anim="peafowl-left"].has-animate {
	animation-name: peafowlLeft;
	animation-duration: 2500ms !important;
	animation-fill-mode: forwards;
	animation-timing-function: cubic-bezier(0.33, 0.12, 0.61, 0.9);
}

@keyframes peafowlRight {
	0% {
		transform: translate3d(50%, 0, 0);
	}
	20% {
		transform: translate3d(60%, 5%, 0);
	}
	40% {
		transform: translate3d(70%, 0, 0);
	}
	60% {
		transform: translate3d(80%, 5%, 0);
	}
	80% {
		transform: translate3d(90%, 0, 0);
	}
	100% {
		transform: translate3d(100%, 5%, 0);
	}
}

@keyframes peafowlLeft {
	0% {
		transform: translate3d(-50%, 0, 0);
	}
	20% {
		transform: translate3d(-60%, 5%, 0);
	}
	40% {
		transform: translate3d(-70%, 0, 0);
	}
	60% {
		transform: translate3d(-80%, 5%, 0);
	}
	80% {
		transform: translate3d(-90%, 0, 0);
	}
	100% {
		transform: translate3d(-100%, 5%, 0);
	}
}
