.couple-section {
	position: relative;
	text-align: center;
	.row-couple {
		row-gap: 5rem;
	}
	.castle-couple {
		position: absolute;
		bottom: 0;
		left: 50%;
		width: 100%;
		transform: translateX(-50%);
		@include max-lg {
			width: 150%;
		}
		@include max-md {
			width: 250%;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
		}
		.orn-3 {
			position: absolute;
			top: 0;
			right: 0;
			width: 140px;
			transform: translate(60%, 0) scaleX(-1);
			@include min-md {
				width: 160px;
				transform: translate(50%, 0) scaleX(-1);
			}
			@include min-lg {
				width: 180px;
			}
			.orn-4 {
				position: absolute;
				bottom: 0;
				right: 0;
				width: 80px;
				transform: translate(-30%, 60%) scaleX(-1);
				@include min-md {
					width: 100px;
				}
				@include min-lg {
					width: 120px;
				}
			}
		}
		.orn-1 {
			position: absolute;
			top: 2%;
			left: 0;
			width: 120px;
			transform: translate(-70%, 0) scaleX(-1);
			@include min-md {
				top: 5%;
				width: 180px;
			}
			@include min-lg {
				width: 240px;
			}
			.orn-2 {
				position: absolute;
				top: 0;
				left: 0;
				width: 120px;
				transform: translate(-10%, -30%);
				@include min-md {
					width: 140px;
				}
				@include min-lg {
					width: 160px;
				}
			}
		}
		.orn-5 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100px;
			transform: translate(-50%, 80%);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-6 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 120px;
			transform: translate(-20%, 40%) scale(-1);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
		.orn-7 {
			position: absolute;
			bottom: 0;
			left: 50%;
			width: 160px;
			transform: translate(-50%, 50%) rotate(-25deg) scaleY(-1);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
		.orn-8 {
			position: absolute;
			bottom: 0;
			left: 70%;
			width: 120px;
			transform: translate(-50%, 40%) scaleY(-1);
			@include min-md {
				width: 140px;
			}
			@include min-lg {
				width: 160px;
			}
		}
		.orn-9 {
			position: absolute;
			bottom: 0;
			left: 15%;
			width: 80px;
			transform: translate(0, 40%) scaleY(-1);
			@include min-md {
				width: 100px;
			}
			@include min-lg {
				width: 120px;
			}
		}
		.orn-10 {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100px;
			transform: translate(-30%, 40%) rotate(20deg) scale(-1);
			@include min-md {
				width: 120px;
			}
			@include min-lg {
				width: 140px;
			}
		}
		.orn-11 {
			position: absolute;
			bottom: 0;
			right: 0;
			width: 160px;
			transform: translate(0%, 40%) rotate(25deg) scale(-1);
			@include min-md {
				width: 180px;
			}
			@include min-lg {
				width: 200px;
			}
		}
	}
	.couple-body {
		position: relative;
		@include max-md {
			.container {
				padding: 0;
			}
		}
	}
	.couple-content {
		position: relative;
		padding: 0 0.5rem;
		margin-bottom: 3rem;
	}
	.couple {
		position: relative;
		text-align: center;
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 7/8;
			margin: 0 auto;
			.image-element {
				position: relative;
				width: 100%;
				height: 100%;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: var(--border-radius);
				}
			}
		}
		.couple-description {
			position: relative;
			padding: 2rem 1.5rem;
			background-color: var(--color-primary);
			color: var(--color-dark);
			width: 90%;
			margin: -5rem auto 0;
			border-radius: var(--border-radius);
			&::before {
				position: absolute;
				top: 50%;
				left: 50%;
				width: calc(100% - 1rem);
				height: calc(100% - 1rem);
				border: 1px solid var(--color-main);
				border-radius: var(--border-radius);
				content: "";
				transform: translate(-50%, -50%);
				pointer-events: none;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.4rem 0.5rem;
			border-radius: 50px;
			margin: 0 auto;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
