.apology-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.appology-wrap {
		position: relative;
		padding: 3rem 2rem;
		background: var(--color-primary);
		border-radius: var(--border-radius);
		color: var(--color-dark);
		@include max-md {
			padding: 2rem 1rem;
		}
		&::before {
			position: absolute;
			top: 50%;
			left: 50%;
			width: calc(100% - 1rem);
			height: calc(100% - 1rem);
			border: 1px solid var(--color-main);
			border-radius: var(--border-radius);
			content: "";
			transform: translate(-50%, -50%);
			pointer-events: none;
		}
	}
}
